/*************** ABOUT PAGE ***************/

:root {
  --gray: #c0bcb5;
  --white: #eeeeee;
  --black: #1c2321;
  --blue: #087e8b;
  --purple: #846075;
  --red: #af5d63;
  --lightblue: rgb(128, 189, 255);
  --green: rbg(96, 108, 56);
  --niceBlue: rgb(36, 82, 69);
}
.aboutPage {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: transparent;
  position: relative;
  top: -12vh;
}

.aboutSection {
  height: 120vh;
  width: 100%;
  display: grid;
  place-items: center;
  margin-top: 10vh;
}

/*************FIRST SECTION*************/

.pictureAndBox {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  position: relative;
}

.aboutPicture {
  height: 300px;
  width: 250px;
  border-radius: 50%;
  margin-bottom: 30px;
}
@media (max-height: 650px) {
  .aboutPicture {
    margin-top: 50px;
    margin-bottom: 50px;
  }
}

.skillBox {
  width: 50vw;
  background-color: transparent;
  margin-left: 10vw;
  display: flex;
  flex-direction: row;
}
@media (max-width: 1017px) {
  .skillBox {
    display: flex;
    flex-direction: column;
    margin-top: -80px;
    background-color: transparent;
  }
  .techIcons {
    margin-bottom: 30px;
  }
}
@media (min-width: 900px) {
  .techIcons {
    margin-right: 45px;
  }
}
.techIcons {
  margin-right: 60px;
}
.techIcons:hover {
  opacity: 1 !important;
  color: black;
}
/****************COLLAGE SECTION********************/
.collage {
  position: relative;
  max-width: auto;
  max-height: 700px;
  opacity: 0.2;
  overflow: hidden;
}
.collageSection {
  background-color: var(--niceBlue);
  display: grid;
  place-items: center;
  overflow: hidden;
  position: relative;
  top: 25vh;
}
.aboutMe {
  color: white;
  justify-self: center;
  text-align: center;
  top: 15vh;
  position: absolute;
}
.aboutMe h1 {
  margin-bottom: 18vh;
  font-size: 5rem;
  font-weight: 800;
}
.aboutMe p {
  font-size: 1.3rem;
  margin-top: 10px;
  margin-left: 20px;
  margin-right: 20px;
  width: 98%;
}

@media (max-width: 900px) {
  .aboutMe h1 {
    margin-bottom: 10vh;
    font-size: 3rem;
    font-weight: 800;
  }
  .aboutMe p {
    font-size: 0.8rem;
    margin-top: 20px;
    margin-right: 0;
    margin-left: 0;
    width: 90%;
  }
}
